import { useState, useEffect } from 'react';
import Image from 'next/image';

const products = [
  { id: 1, name: 'Modern Chair', price: '120', image: '/7.png' },
  { id: 2, name: 'Wooden Table', price: '350', image: '/8.png' },
  { id: 3, name: 'Elegant Sofa', price: '790', image: '/9.png' },
];

const MyDashboard = () => {
return (
<div className="font-sans text-gray-800">
      {/* Header */}
      <header className="bg-white shadow p-4 flex justify-between items-center">
        <h1 className="text-2xl font-bold text-green-700">FurniSpace</h1>
        <nav>
          <ul className="flex gap-6">
            <li><a href="/index" className="hover:text-green-600">Home</a></li>
            <li><a href="/product" className="hover:text-green-600">Product</a></li>
            <li><a href="/contact" className="hover:text-green-600">Contact Us</a></li>
            <li><a href="/about" className="hover:text-green-600">About</a></li>
          </ul>
        </nav>
      </header>

      {/* Hero */}
      <section className="bg-green-50 py-20 px-4 text-center">
        <h2 className="text-4xl font-bold mb-4">Modern Furniture for Every Home</h2>
        <p className="text-lg text-gray-600 mb-6">Affordable, stylish, and built to last.</p>
        <button className="bg-green-600 text-white px-6 py-3 rounded-full hover:bg-green-700 transition">
          Shop Now
        </button>
      </section>

      {/* Featured Products */}
      <section className="py-16 px-4">
        <h3 className="text-3xl font-semibold text-center mb-10">Featured Products</h3>
        <div className="grid gap-8 sm:grid-cols-2 md:grid-cols-3">
          {products.map(product => (
            <div key={product.id} className="bg-white p-4 rounded shadow text-center">
              <Image src={product.image} width={300} height={200} alt={product.name} className="mx-auto mb-4 rounded" />
              <h4 className="text-xl font-medium">{product.name}</h4>
              <p className="text-green-600 text-lg font-semibold">{product.price}</p>
              <button className="mt-4 px-4 py-2 bg-green-600 text-white rounded hover:bg-green-700 transition">
                Add to Cart
              </button>
            </div>
          ))}
        </div>
      </section>

      {/* Footer */}
      <footer className="bg-gray-800 text-white py-6 text-center">
        <p>&copy; 2025 FurniSpace. All rights reserved.</p>
      </footer>
    </div>
 );
};

export default MyDashboard;